import React from "react";
import { Text, View, ScrollView } from "react-native";
import { ThemeContext } from "/components/ThemeProvider";
import { FontSizeContext } from "/components/FontSizeProvider";
import Header from "/components/Header";
import Paragraph from "/components/Paragraph";
import resolveStyles from "/styles/subpage";
import ListItem from "/components/ListItem";

export default function Antistresova({ navigation }) {
  const { computeTheme } = React.useContext(ThemeContext);
  const { computeFontSize } = React.useContext(FontSizeContext);
  const styles = resolveStyles(computeTheme(), computeFontSize);

  const goTo = (dest) => {
    navigation.push(dest);
  };

  return (
    <View>
      <Header backButton navigation={navigation} />
      <ScrollView>
        <View style={styles.container}>
          <Text style={styles.header}> </Text>
          <Paragraph style={{ paddingBottom: 20 }}>
              ,       .     
               ,     .     ,    -
            .     ,     .    
               ,        .
          </Paragraph>

          <ListItem roundTop title=" " onPress={() => goTo("Tilesni")} />
          <ListItem title=" " onPress={() => goTo("Relaksaciini")} />
          <ListItem title=" " onPress={() => goTo("Kognitivni")} />
          <ListItem roundBottom title=" " onPress={() => goTo("Dinalni")} />

          <View style={styles.spacer} />
        </View>
      </ScrollView>
    </View>
  );
}
